.dropzone{min-height:165px;border:2px dashed #757488;background:#f8f8f8;display:flex;justify-content:center;align-items:center}.dropzone:focus{outline:0}.dropzone-dummy{margin-top:30px;min-height:100px;border:1px dotted #757488;display:flex;justify-content:center;align-items:center}.dropzone-dummy:focus{outline:0}.dz-thumb{display:inline-flex;border-radius:2px;border:1px solid #eaeaea;margin-bottom:8px;margin-right:8px;width:100px;height:100px;padding:4px;box-sizing:border-box}.thumb-container{display:flex;flex-direction:row;flex-wrap:wrap;margin-top:16px}.dz-thumb-inner{min-width:0}.dz-img{display:block;width:100%;height:100%}
